.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
}

.dialogsItems {
    padding: 10px;
    color: #fff;
}

.messages {
    padding: 10px;
}

.messages .message {
    color: #fff;
}
.active {
    color: gold;
    text-decoration: none;
}

.NewMessages {
    display: flex;
    background: green;
}



